<template>
  <YPage :logo="require('@/assets/img/logo_white.png')">
    <div class="s-room-wrapper">
      <div class="room-hd">
        <table>
          <tr>
            <th></th>
            <th>已还</th>
            <th>剩下</th>
            <th>总共</th>
          </tr>
          <tr>
            <td>本金</td>
            <td v-text="principaled"></td>
            <td v-text="principalLeft"></td>
            <td v-text="principalTotal"></td>
          </tr>
          <tr>
            <td>利息</td>
            <td v-text="interested"></td>
            <td v-text="interestLeft"></td>
            <td v-text="interestTotal"></td>
          </tr>
          <tr>
            <td>合计</td>
            <td v-text="reimbursed"></td>
            <td v-text="reimburseLeft"></td>
            <td v-text="reimburseTotal"></td>
          </tr>
        </table>
        <div class="room-tab">
          <div class="tab">
            <span :class="{'active':current === index}" @click="onChange(index)" v-for="(item,index) in tabs" :key="index" v-text="item"></span>
          </div>
        </div>
      </div>
      <div class="room-bd">
        <table>
          <tr>
            <th>序号</th>
            <th>时间</th>
            <th>每月</th>
            <th>利息</th>
            <th>本金</th>
            <th>剩余</th>
          </tr>
          <tr v-for="item in newList" :key="item.index">
            <td v-text="item.index"></td>
            <td v-text="item.datetime"></td>
            <td v-text="item.reimburse"></td>
            <td v-text="item.interest"></td>
            <td v-text="item.principal"></td>
            <td v-text="item.residualPrincipal"></td>
          </tr>
        </table>
      </div>
    </div>
  </YPage>
</template>

<script>
import YPage from '@/components/YPage.vue'
import roomList from "@/mock/roomList"
export default {
  components: {
    YPage
  },
  data() {
    return {
      tabs:['全 部','剩 下','已 还'],
      current:1,
      reimburseTotal:0,
      reimbursed:0,
      reimburseLeft:0,
      interestTotal:0,
      interested:0,
      interestLeft:0,
      principalTotal:0,
      principaled:0,
      principalLeft:0,
      allList:[],
      leftList:[],
      pastList:[],
      newList:[]
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    onChange(e){
      this.current = e;
      let value = parseInt(e);
      if(value === 0){
        this.newList = this.allList;
      }else if(value === 1){
        this.newList = this.leftList;
      }else if(value === 2){
        this.newList = this.pastList;
      }
    },
    init(){
      let params = this.$route.query;
      let date = new Date();
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      if(params && params.y && params.m){
        y = params.y;
        m = params.m;
      }
      let mm = m < 10? `0${m}`: m;
      let localDate = new Date(`${y}-${mm}`);
      // let localDateStr = `${y}年${mm}月`;
      let leftList=[],pastList = [];
      let reimburseTotal = 0,reimbursed = 0,reimburseLeft = 0;
      let interestTotal = 0,interested = 0,interestLeft = 0;
      let principalTotal = 0,principaled = 0,principalLeft = 0;
      for (let i = 0,len = roomList.length; i < len; i++){
        reimburseTotal += roomList[i].reimburse * 100;
        interestTotal += roomList[i].interest * 100;
        principalTotal += roomList[i].principal * 100;
        let iArr = roomList[i].datetime.split(/年|月/);
        let iDate = new Date(`${iArr[0]}-${iArr[1]}`);
        if(localDate.getTime() > iDate.getTime()){
          reimbursed += roomList[i].reimburse * 100;
          interested += roomList[i].interest * 100;
          principaled += roomList[i].principal * 100;
          pastList.push(roomList[i]);
        }else{
          leftList.push(roomList[i]);
          reimburseLeft += roomList[i].reimburse * 100;
          interestLeft += roomList[i].interest * 100;
          principalLeft += roomList[i].principal * 100;
        }
      }
      this.reimburseTotal = reimburseTotal / 100; //730,733.00
      this.reimbursed = reimbursed / 100;
      this.reimburseLeft = reimburseLeft/100;
      this.interestTotal = interestTotal / 100; //利息总额 250,733.00
      this.interested = interested / 100;
      this.interestLeft = interestLeft/100;
      this.principalTotal = principalTotal / 100;
      this.principaled = principaled / 100;
      this.principalLeft = principalLeft/100;
      this.newList = leftList;
      this.leftList= leftList;
      this.pastList = pastList;
      this.allList = roomList;
    }
  }
}
</script>

<style lang="scss" scoped>
@media (min-width: 576px) {
  .s-room-wrapper {
    background-color: #fff;
    width: 1020px;
    margin: 10px auto;
    padding: 30px;
    table{
      width: 100%;
      th{
        padding: 5px 10px;
        font-size: 14px;
        line-height: 20px;
        border: 1px solid #e2e9f5;
        background-color: #ebeff5;
      }
      td{
        padding: 5px 10px;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        border: 1px solid #e2e9f5;
      }
      tr{
        &:nth-child(odd){
          background-color: #f5f7fa;
        }
      }
    }

    .room-hd{
      padding-bottom: 20px;
      .room-tab{
        padding-top: 20px;
        .tab{
          display: flex;
          span{
            display: block;
            padding: 5px 20px;
            color: #333;
            border-top: 1px solid #e6e6e6;
            border-bottom: 1px solid #e6e6e6;
            box-sizing: border-box;
            cursor: pointer;
            &:first-child{
              border-radius: 3px 0 0 3px;
              border-left: 1px solid #e6e6e6;
            }
            &:last-child{
              border-radius: 0 3px 3px 0;
              border-right: 1px solid #e6e6e6;
            }
            &.active{
              color: var(--color-primary);
              border: 1px solid var(--color-primary);
              &+span{
                border-left: none;
              }
            }
          }
          span + span{
            border-left: 1px solid #e6e6e6;
          }
        }
      }
    }
  }
}


@media (max-width: 576px) {
  .s-room-wrapper {
    background-color: #fff;
    width: auto;
    margin: 10px 0;
    padding: 15px;
    table{
      width: 100%;
      th{
        padding: 5px;
        font-size: 12px;
        line-height: 18px;
        border: 1px solid #e2e9f5;
        background-color: #ebeff5;
      }
      td{
        padding: 5px;
        font-size: 12px;
        line-height: 18px;
        text-align: center;
        border: 1px solid #e2e9f5;
      }
      tr{
        &:nth-child(odd){
          background-color: #f5f7fa;
        }
      }
    }

    .room-hd{
      padding-bottom: 15px;
      .room-tab{
        padding-top: 15px;
        .tab{
          display: flex;
          span{
            display: block;
            flex: 1;
            padding: 5px 20px;
            color: #333;
            border-top: 1px solid #e6e6e6;
            border-bottom: 1px solid #e6e6e6;
            box-sizing: border-box;
            cursor: auto;
            width: 0;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: break-all;
            text-align: center;
            &:first-child{
              border-radius: 3px 0 0 3px;
              border-left: 1px solid #e6e6e6;
            }
            &:last-child{
              border-radius: 0 3px 3px 0;
              border-right: 1px solid #e6e6e6;
            }
            &.active{
              color: var(--color-primary);
              border: 1px solid var(--color-primary);
              &+span{
                border-left: none;
              }
            }
          }
          span + span{
            border-left: 1px solid #e6e6e6;
          }
        }
      }
    }

    .room-bd{
      overflow: auto;

    }
  }
}
</style>
